<template>
  <div class="form-create-order">
    <vue-form>
      <div class="form__switcher">
        <label>Кто нужен?</label>
        <vue-switcher v-model="orderSwitcherParams" :disabled="disabledItems" />
      </div>
      <div class="first-option" v-if="orderSwitcherParams[0].isSwitch === true">
        <div class="form__location" v-if="!model.orderCheckBoxParams">
          <label>Адрес</label>
          <vue-text-input
            v-model="model.locationEnd"
            :placeholder="'Адрес'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="model.inputTime.time"
            v-model:is-valid="model.inputTime.isValid"
            :type="'time'"
            :placeholder="'12:00'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков</label>
          <vue-number-input
            v-model="model.inputPeople.peopleCount"
            v-model:is-valid="model.inputPeople.isValid"
            :placeholder="'Кол-во'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__description">
          <label>Описание проекта</label>
          <vue-text-area
            v-model="model.orderDescription.description"
            v-model:is-valid="model.orderDescription.isValid"
            :placeholder="'Что нужно сделать?'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__working-time">
          <label>Ориентировочное время занятости (в минутах)</label>
          <vue-number-input
            v-model="model.inputWorkingTime.time"
            v-model:is-valid="model.inputWorkingTime.isValid"
            :placeholder="'60'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="model.inputPrice.price"
            v-model:is-valid="model.inputPrice.isValid"
            :placeholder="'500'"
            :disabled="disabledItems"
          />
        </div>
      </div>

      <div
        class="second-option"
        v-if="orderSwitcherParams[0].isSwitch === false"
      >
        <div class="form__location-transportation__start">
          <label>Пункт "А"</label>
          <vue-text-input
            v-model="model.locationStart"
            :placeholder="'Адрес'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__location-transportation__end">
          <label>Пункт "Б"</label>
          <vue-text-input
            v-model="model.locationEnd"
            :placeholder="'Адрес'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__time">
          <label>В какое время нужно прибыть?</label>
          <vue-number-input
            v-model="model.inputTime.time"
            v-model:is-valid="model.inputTime.isValid"
            :type="'time'"
            :placeholder="'12:00'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__people">
          <label>Нужное количество грузчиков (если нет, то впишите 0)</label>
          <vue-number-input
            v-model="model.inputPeople.peopleCount"
            v-model:is-valid="model.inputPeople.isValid"
            :placeholder="'Кол-во'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__description">
          <label>Что перевозить?</label>
          <vue-text-area
            v-model="model.orderDescription.description"
            v-model:is-valid="model.orderDescription.isValid"
            :placeholder="'Что нужно сделать?'"
            :disabled="disabledItems"
          />
        </div>
        <div class="form__price">
          <label>Цена за заказ (руб)</label>
          <vue-number-input
            v-model="model.inputPrice.price"
            v-model:is-valid="model.inputPrice.isValid"
            :placeholder="'500'"
            :disabled="disabledItems"
          />
        </div>
      </div>
      <div class="form__edit">
        <vue-yellow-button
          :title="stateBtnEdit.title"
          @click="onEditTemplate"
          v-loading="isLoadingEditTemplate"
        />
        <vue-red-button
          :title="'Удалить'"
          @click="onDeleteTemplate(props.template.id)"
          v-loading="isLoadingDeleteTemplate"
        />
      </div>
    </vue-form>
  </div>
</template>

<script setup lang="ts">
import VueForm from "@/components/Forms/VueForm.vue";
import VueTextInput from "@/components/Inputs/VueTextInput.vue";
import VueTextArea from "@/components/Inputs/VueTextArea.vue";
import VueSwitcher from "@/components/Switchers/VueSwitcher.vue";
import VueNumberInput from "@/components/Inputs/VueNumberInput.vue";
import VueYellowButton from "@/components/Buttons/VueYellowButton.vue";
import VueRedButton from "../Buttons/VueRedButton.vue";

import { ref, defineProps, defineEmits, computed, reactive } from "vue";
import { Template } from "@/interfaces/Template";
import { userTemplatesStore } from "@/store/template";
import notification from "@/notifications";

const props = defineProps({
  template: {
    type: Object as () => Template,
    required: true,
  },
  disabled: {
    type: Boolean,
    required: false,
    default: true,
  },
});

const emits = defineEmits(["update:disabled"]);

const templateStore = userTemplatesStore();
const disabledItems = computed(() => props.disabled);
const isLoadingEditTemplate = ref(false);
const isLoadingDeleteTemplate = ref(false);

let stateBtnEdit = {
  title: "Редактировать",
  localState: "edit",
};

const orderSwitcherParams = ref([
  {
    id: 1,
    value: "Грузчики",
    isSwitch: false,
  },
  {
    id: 2,
    value: "Грузоперевозки",
    isSwitch: false,
  },
]);

const model = reactive({
  orderDescription: {
    description: "",
    isValid: false,
  },
  locationStart: props.template?.locationStart || "",
  locationEnd: props.template?.locationEnd || "",
  inputPeople: {
    peopleCount: props.template?.sumPeople || null,
    isValid: false,
  },
  inputPrice: {
    price: props.template?.priceInfo.price || null,
    isValid: false,
  },
  inputWorkingTime: {
    time: props.template?.workingHours || null,
    isValid: false,
  },
  inputTime: {
    time: props.template?.time || "",
    isValid: false,
  },
  orderCheckBoxParams: null,
  workers: "",
});

const onEditTemplate = () => {
  if (stateBtnEdit.localState === "edit") {
    stateBtnEdit.title = "Сохранить";
    stateBtnEdit.localState = "save";
    emits("update:disabled", false);
  } else {
    isLoadingEditTemplate.value = true;

    //TODO(tolstov): симуляция запроса
    setTimeout(() => {
      updateTemplate();
      stateBtnEdit.title = "Редактировать";
      stateBtnEdit.localState = "edit";

      isLoadingEditTemplate.value = false;
      notification.success("Сохранено!");
    }, 1000);
  }
};

const onDeleteTemplate = (id: number) => {
  isLoadingDeleteTemplate.value = true;

  setTimeout(() => {
    templateStore.deleteTemplate(id);

    //TODO(tolstov): записать ответ удаления в переменную и при положительном ответе высветить уведомление
    isLoadingDeleteTemplate.value = false;
    notification.warning("Шаблон удален!");
  }, 1000);
};

const updateTemplate = () => {
  const template = {
    id: props.template!.id,
    locationStart: model.locationStart,
    locationEnd: model.locationEnd,
    time: model.inputTime.time,
    name: props.template.name,
    sumPeople: Number(model.inputPeople.peopleCount),
    description: model.orderDescription.description,
    workingHours: model.inputWorkingTime.time,
    workers: {
      value: model.workers,
    },
    priceInfo: {
      price: model.inputPrice.price,
      typeFormat: 1, //добавить тип оплаты - час или сдельно
      typePayment: "Диспетчер", //добавить тип оплаты комиссии
    },
  } as Template;

  templateStore.editTemplate(template);
  emits("update:disabled", true);
};

if (props.template) {
  model.workers = props.template.workers.value;
  orderSwitcherParams.value.find((item) => {
    if (item.value === model.workers) {
      item.isSwitch = true;
    }
  });
}
</script>

