<template>
  <div class="template-create">
    <div class="template-create__title h2">
      <h2>Создание шаблона</h2>
    </div>
  </div>
  <vue-form>
    <div class="form__template-name">
      <vue-text-input
        v-model="model.templateName"
        :placeholder="'Название шаблона'"
      />
    </div>
    <div class="form__switcher" v-if="hasTemplateName">
      <label>Для кого шаблон?</label>
      <vue-switcher v-model="model.orderSwitcherParams" />
    </div>
    <div
      class="first-option"
      v-if="model.orderSwitcherParams[0].isSwitch === true"
    >
      <div class="form__location">
        <label>Адрес</label>
        <vue-text-input v-model="model.locationEnd" :placeholder="'Адрес'" />
      </div>
      <div class="form__time">
        <label>В какое время нужно прибыть?</label>
        <vue-number-input
          v-model="model.inputTime.time"
          v-model:is-valid="model.inputTime.isValid"
          :type="'time'"
          :placeholder="'12:00'"
        />
      </div>
      <div class="form__people">
        <label>Нужное количество грузчиков</label>
        <vue-number-input
          v-model="model.inputPeople.peopleCount"
          :placeholder="'Кол-во'"
          v-model:is-valid="model.inputPeople.isValid"
        />
      </div>
      <div class="form__description">
        <label>Описание проекта</label>
        <vue-text-area
          v-model="model.orderDescription.description"
          v-model:is-valid="model.orderDescription.isValid"
          :placeholder="'Что нужно сделать?'"
        />
      </div>
      <div class="form__working-time">
        <label>Ориентировочное время занятости (в минутах)</label>
        <vue-number-input
          v-model="model.inputWorkingTime.time"
          v-model:is-valid="model.inputWorkingTime.isValid"
          :placeholder="'60'"
        />
      </div>
      <div class="form__price">
        <label>Цена за заказ (руб)</label>
        <vue-number-input
          v-model="model.inputPrice.price"
          v-model:is-valid="model.inputPrice.isValid"
          :placeholder="'500'"
        />
      </div>
      <div class="form__save-template">
        <vue-black-button
          :title="'Сохранить шаблон'"
          @click="onSaveNewTemplate(newTemplateModel)"
        />
      </div>
    </div>

    <div
      class="second-option"
      v-if="model.orderSwitcherParams[1].isSwitch === true"
    >
      <div class="form__location-transportation__start">
        <label>Пункт "А"</label>
        <vue-text-input v-model="model.locationStart" :placeholder="'Адрес'" />
      </div>
      <div class="form__location-transportation__end">
        <label>Пункт "Б"</label>
        <vue-text-input v-model="model.locationEnd" :placeholder="'Адрес'" />
      </div>
      <div class="form__time">
        <label>В какое время нужно прибыть?</label>
        <vue-number-input
          v-model="model.inputTime.time"
          v-model:is-valid="model.inputTime.isValid"
          :type="'time'"
          :placeholder="'12:00'"
        />
      </div>
      <div class="form__people">
        <label>Нужное количество грузчиков (если нет, то впишите 0)</label>
        <vue-number-input
          v-model="model.inputPeople.peopleCount"
          :placeholder="'Кол-во'"
          v-model:is-valid="model.inputPeople.isValid"
        />
      </div>
      <div class="form__description">
        <label>Что перевозить?</label>
        <vue-text-area
          v-model="model.orderDescription.description"
          v-model:is-valid="model.orderDescription.isValid"
          :placeholder="'Что нужно сделать?'"
        />
      </div>
      <div class="form__price">
        <label>Цена за заказ (руб)</label>
        <vue-number-input
          v-model="model.inputPrice.price"
          v-model:is-valid="model.inputPrice.isValid"
          :placeholder="'500'"
        />
      </div>
      <div class="form__save-template" v-loading="isLoadingCreateTemplate">
        <vue-black-button
          @click="onSaveNewTemplate(newTemplateModel)"
          :title="'Сохранить шаблон'"
        />
      </div>
    </div>
  </vue-form>
</template>
<script lang="ts" setup>
import VueForm from "./VueForm.vue";
import VueNumberInput from "../Inputs/VueNumberInput.vue";
import VueTextInput from "../Inputs/VueTextInput.vue";
import VueTextArea from "../Inputs/VueTextArea.vue";
import VueSwitcher from "../Switchers/VueSwitcher.vue";
import VueBlackButton from "../Buttons/VueBlackButton.vue";

import { userTemplatesStore } from "@/store/template";
import { computed, watch, reactive, defineEmits, ref } from "vue";
import { TemplateBase } from "@/interfaces/TemplateBase";
import notification from "@/notifications";

const templateStore = userTemplatesStore();

const emits = defineEmits(["saved-template"]);

const hasTemplateName = computed((): boolean => {
  return !!model.templateName;
});

const isLoadingCreateTemplate = ref(false);

let model = reactive({
  templateName: "",
  orderSwitcherParams: [
    {
      id: 1,
      value: "Грузчики",
      isSwitch: false,
    },
    {
      id: 2,
      value: "Грузоперевозки",
      isSwitch: false,
    },
  ],
  orderDescription: {
    description: "",
    isValid: false,
  },
  locationStart: "",
  locationEnd: "",
  inputPeople: {
    peopleCount: null as number | null,
    isValid: false,
  },
  inputPrice: {
    price: null as number | null,
    isValid: false,
  },
  inputWorkingTime: {
    time: null,
    isValid: false,
  },
  inputTime: {
    time: "",
    isValid: false,
  },
});

let newTemplateModel: TemplateBase;

watch(model, (newVal) => {
  if (newVal) {
    newTemplateModel = {
      name: model.templateName,
      locationStart: model.locationStart,
      locationEnd: model.locationEnd,
      time: model.inputTime.time,
      sumPeople: model.inputPeople.peopleCount,
      description: model.orderDescription.description,
      workers: {
        value: model.orderSwitcherParams[0].isSwitch
          ? model.orderSwitcherParams[0].value
          : model.orderSwitcherParams[1].value,
      },
      workingHours: model.inputWorkingTime.time,
      priceInfo: {
        price: model.inputPrice.price,
      },
    };
  }
});

const onSaveNewTemplate = (model: TemplateBase) => {
  isLoadingCreateTemplate.value = true;

  setTimeout(() => {
    const result = templateStore.createdTemplate(model);
    if (result) {
      notification.success("Шаблон сохранен!");
      isLoadingCreateTemplate.value = true;
    } else {
      notification.error("Ошибка создания шаблона!");
    }
    emits("saved-template", false);
  }, 1500);
};
</script>
